
import { defineComponent, ref } from 'vue'
import { useRouter } from 'vue-router'
import { useStore } from 'vuex'
import ValidateInput, { RulesType } from '../components/ValidateInput.vue'
import ValidateForm from '../components/ValidateForm.vue'
import createMessage from '@/components/createMessage'

export default defineComponent({
  name: 'HomeView',
  components: {
    ValidateInput,
    ValidateForm
  },
  setup () {
    const router = useRouter()
    const store = useStore()
    const emailVal = ref('')
    // 选择验证邮箱有效性的规则
    const emailRules: RulesType = [
      { type: 'required', message: '输入不能为空' },
      { type: 'email', message: '邮箱格式无效' }
    ]
    const passwordVal = ref('')
    // 选择验证密码有效性的规则
    const passwordRules: RulesType = [
      { type: 'required', message: '输入不能为空' }
    ]
    // 输出表单验证结果，如果表单验证通过，执行某些操作
    const onFormResult = (result: boolean) => {
      if (result) {
        const loginData = {
          email: emailVal.value,
          password: passwordVal.value
        }
        // login的同时获得当前用户的信息
        store.dispatch('loginAndFetch', loginData).then(() => {
          createMessage('success', '登录成功 2秒后跳转首页')
          setTimeout(() => router.push('/'), 2000)
        }).catch((err) => {
          console.log('err', err) // 错误处理不是个别的，是全局的，详见main.ts中的axios拦截器
        })
      }
    }
    return {
      emailRules,
      emailVal,
      passwordRules,
      passwordVal,
      onFormResult
    }
  }
})
