import { ColumnType, UserType } from '@/store'

interface CheckCondition {
  format?: string[];
  size?: number;
}
type ErrorType = 'format' | 'size' | null

export function beforeUploadCheck (file: File, condition: CheckCondition) {
  const { format, size } = condition
  const formatIsValid = format ? format.includes(file.type) : true
  const sizeIsValid = size ? file.size / 1024 / 1024 < size : true // size的单位原本是B，将B转换为MB
  let error: ErrorType = null
  if (!sizeIsValid) {
    error = 'size'
  }
  if (!formatIsValid) {
    error = 'format'
  }
  return {
    passed: formatIsValid && sizeIsValid,
    error
  }
}

export function addDefaultAvatar (data: ColumnType | UserType) {
  if (!data.avatar) {
    data.avatar = { defaultUrl: require('@/assets/column.jpg') }
  }
}
