
import { defineComponent, computed, onMounted, ref, watch } from 'vue'
import { useRoute } from 'vue-router'
import { useStore } from 'vuex'
import PostList from '../components/PostList.vue'
import { GlobalDataType } from '@/store'
import { addDefaultAvatar } from '@/helper'

export default defineComponent({
  components: {
    PostList
  },
  setup () {
    const route = useRoute()
    const store = useStore<GlobalDataType>()
    const currentId = ref(route.params.id)

    onMounted(() => {
      store.dispatch('fetchColumn', currentId.value)
      store.dispatch('fetchPosts', currentId.value)
    })

    watch(() => route.params.id, (newId) => {
      store.dispatch('fetchColumn', newId)
      store.dispatch('fetchPosts', newId)
      currentId.value = newId
    })

    const column = computed(() => {
      const selectColumn = store.getters.findColumnById(currentId.value)
      if (selectColumn) {
        addDefaultAvatar(selectColumn)
      }
      return selectColumn
    }) // 从Column中找到对应的column，并为它添加默认头像

    const post = computed(() => store.getters.filterPostById(currentId.value)) // 注意要用computed包裹

    return {
      column,
      post
    }
  }
})
