
import { defineComponent, PropType, ref } from 'vue'

export type MessageType = 'success' | 'error' | 'default' // 在createMessage中引入
export default defineComponent({
  props: {
    message: String,
    type: {
      type: String as PropType<MessageType>,
      default: 'default'
    }
  },
  setup (props) {
    const isVisible = ref(true) // 在App组件中也使用了v-if来控制本组件的显示与否
    const hide = () => {
      isVisible.value = false
    }
    const classObject = {
      'alert-success': props.type === 'success', // 'alert-success'是否存在取决于它的真假值，下同
      'alert-danger': props.type === 'error',
      'alert-primary': props.type === 'default'
    }
    return {
      hide,
      classObject,
      isVisible
    }
  }
})
