
import { defineComponent, onUnmounted } from 'vue'
import mitt from 'mitt'

// 既是collection函数中参数的类型，也是formMitt的参数类型
interface ValidateFunc {
  (): boolean
}
// 这个定义是让formMitt的参数和collection函数的参数对应
type FormMitt = {
  'form-item-created': ValidateFunc
}

export const formMitt = mitt<FormMitt>() // 在ValidateInput中导入
export default defineComponent({
  emits: ['form-result'],
  setup (props, context) {
    let funcArr:ValidateFunc[] = []
    const submitForm = () => {
      // 循环验证每个ValidateInput，如果通过，将最终结果发送给上层组件（emit），为了测试，上层组件将打印结果
      const result = funcArr.map(func => func()).every(singleResult => singleResult)
      context.emit('form-result', result) // 注意这里有一个参数，所以接受的时候要传参
    }
    const collection = (func:ValidateFunc) => { // 需要用ts约束一下传入的参数
      funcArr.push(func)
    }
    // 监听form-item-created事件，并接收validateInput函数
    formMitt.on('form-item-created', collection)
    onUnmounted(() => {
      formMitt.off('form-item-created', collection)
      funcArr = []
    })
    return {
      submitForm
    }
  }
})
