
import { defineComponent, ref, onMounted } from 'vue'
import { useRouter, useRoute } from 'vue-router'
import { useStore } from 'vuex'
import { GlobalDataType, ColumnPostType, ResponseType, ImageType } from '@/store'
import createMessage from '@/components/createMessage'
import ValidateForm from '../components/ValidateForm.vue'
import ValidateInput, { RulesType } from '../components/ValidateInput.vue'
import UploadBtn from '@/components/UploadBtn.vue'
import { beforeUploadCheck } from '@/helper'

export default defineComponent({
  components: {
    ValidateForm,
    ValidateInput,
    UploadBtn
  },
  setup () {
    const uploadedImage = ref() // 接收异步请求得到的post数据
    const titleVal = ref('') // 创建响应式数据
    const contentVal = ref('')
    let imageId = ''
    const router = useRouter()
    const route = useRoute()
    const store = useStore<GlobalDataType>()
    const isEditMode = !!route.query.id

    onMounted(() => {
      if (isEditMode) {
        store.dispatch('fetchPost', route.query.id).then((rawData: ResponseType<ColumnPostType>) => {
          const currentPost = rawData.data
          if (currentPost.image) {
            uploadedImage.value = currentPost.image
          }
          titleVal.value = currentPost.title
          contentVal.value = currentPost.content || ''
        })
      }
    })

    const titleRules:RulesType = [
      { type: 'required', message: '输入不能为空' }
    ]
    const contentRules:RulesType = [
      { type: 'required', message: '输入不能为空' }
    ]

    const checkImg = (file: File) => { // 在将图片发送给服务器之前检查图片
      const result = beforeUploadCheck(file, { format: ['image/jpeg', 'image/png'], size: 1 })
      const { passed, error } = result
      if (error === 'format') {
        createMessage('error', '上传格式必须是JPG/PNG')
      }
      if (error === 'size') {
        createMessage('error', '上传大小不能超过1Mb')
      }
      return passed
    }

    const onFileUploaded = (rawData: ResponseType<ImageType>) => { // 图片上传成功后的操作，可以从中拿到Image的id
      createMessage('success', '传图片成功')
      if (rawData.data._id) {
        imageId = rawData.data._id
      }
    }

    const onFormResult = (result: boolean) => { // 表单验证通过后，将数据发给服务器
      if (result) {
        const { column, _id } = store.state.user // 解构赋值,column是column的ID
        if (column) {
          const newPost:ColumnPostType = {
            title: titleVal.value, // 访问ref值需要.value
            content: contentVal.value, // columnId = columnId的简写
            column,
            author: _id
          }
          if (imageId) {
            newPost.image = imageId // image可能不存在，因为上传头图不是必需
          }
          const actionName = isEditMode ? 'updatePost' : 'createPost'
          const sendData = isEditMode ? { id: route.query.id, payload: newPost } : newPost
          store.dispatch(actionName, sendData).then(() => {
            createMessage('success', '上传文章成功 2秒后跳转专栏')
            setTimeout(() => {
              router.push({ name: 'column', params: { id: column } })
            }, 2000)
          })
        }
      }
    }

    return {
      titleRules,
      titleVal,
      contentRules,
      contentVal,
      uploadedImage,
      checkImg,
      onFileUploaded,
      onFormResult
    }
  }
})
