
import { defineComponent, ref, PropType, watch } from 'vue'
import axios from 'axios'
import { ImageType } from '@/store'

type FileStatusType = 'ready' | 'loading' | 'success' | 'error'
type CheckFunc = (file: File) => boolean // 规定传入的参数必须是“File"格式
export default defineComponent({
  props: {
    action: {
      type: String,
      required: true
    },
    checkFunc: {
      type: Function as PropType<CheckFunc>
    },
    uploaded: {
      type: Object as PropType<ImageType>
    }
  },
  inheritAttrs: false, // 禁止attributes直接透传到根节点
  emits: ['fileUploaded', 'fileUploadError'],
  setup (props, context) {
    const uploadedImage = ref<ImageType>()
    const fileStatus = ref<FileStatusType>('ready')
    const fileInput = ref<HTMLInputElement | null>(null) // 拿到DOM节点

    watch(() => props.uploaded, (newValue) => { // 使用watch监听props.uploaded的变化，因为这个属性的值是通过异步请求得到的。得到属性值时，就赋给uploadedImage这个响应式变量。
      if (newValue) {
        fileStatus.value = 'success' // 如果不把fileStatus切换成success，图片无法显示
        uploadedImage.value = newValue
      }
    })

    const setStatusReady = () => {
      fileStatus.value = 'ready'
    }
    const clickFileInput = () => {
      if (fileInput.value) { // 由于fileInput是ref对象，所以要通过value访问DOM节点
        fileInput.value.click()
      }
    }
    const handleFileChange = () => {
      if (fileInput.value?.files) {
        const file = fileInput.value.files[0] // 拿到上传的第一个文件
        if (props.checkFunc) { // checkFunc在上传之前检查文件
          const result = props.checkFunc(file)
          if (!result) {
            return // return直接结束handleFileChange()
          }
        }
        fileStatus.value = 'loading'
        const fileData = new FormData()
        fileData.append('file', file) // 形成键值对的表单数据
        axios.post(props.action, fileData, {
          headers: { 'Content-Type': 'multipart/form-data' }
        }).then(resp => {
          fileStatus.value = 'success'
          uploadedImage.value = resp.data.data
          context.emit('fileUploaded', resp.data)
        }).catch(err => {
          fileStatus.value = 'error'
          console.log('err', err)
        }).finally(() => {
          if (fileInput.value) {
            fileInput.value.value = ''
          }
        })
      }
    }
    return {
      uploadedImage,
      fileStatus,
      fileInput,
      clickFileInput,
      handleFileChange,
      setStatusReady
    }
  }
})
