
import { defineComponent, reactive } from 'vue'
import ValidateForm from '@/components/ValidateForm.vue'
import ValidateInput, { RulesType } from '@/components/ValidateInput.vue'
import axios from 'axios'
import createMessage from '@/components/createMessage'
import router from '@/router'

export default defineComponent({
  components: {
    ValidateForm,
    ValidateInput
  },
  setup () {
    const signupData = reactive({
      email: '',
      nickname: '',
      password: '',
      repeatPassword: ''
    })
    // 选择验证邮箱有效性的规则
    const emailRules: RulesType = [
      { type: 'required', message: '输入不能为空' },
      { type: 'email', message: '邮箱格式无效' }
    ]
    // 选择验证昵称有效性的规则
    const nicknameRules: RulesType = [
      { type: 'required', message: '输入不能为空' }
    ]
    // 选择验证密码有效性的规则
    const passwordRules: RulesType = [
      { type: 'required', message: '输入不能为空' }
    ]
    // 选择验证重复密码有效性的规则
    const repeatPasswordRules: RulesType = [
      { type: 'required', message: '输入不能为空' },
      {
        type: 'customFunc',
        message: '两次密码输入不一致',
        customFunc: () => {
          return signupData.repeatPassword === signupData.password
        }
      }
    ]
    const onFormResult = (result: boolean) => {
      if (result) {
        axios.post('/users', {
          email: signupData.email, nickName: signupData.nickname, password: signupData.password
        }).then(() => {
          createMessage('success', '注册成功 2秒后跳转登录页面')
          setTimeout(() => router.push('/login'), 2000)
        }).catch((err) => console.log(err))
      }
    }
    return {
      signupData,
      emailRules,
      nicknameRules,
      passwordRules,
      repeatPasswordRules,
      onFormResult
    }
  }
})
