
import { defineComponent, PropType, reactive, onMounted, computed } from 'vue'
import { formMitt } from './ValidateForm.vue'

const emailReg = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(.[a-zA-Z0-9])+/

// 定义规则的类型并导出
interface RuleType {
  type: 'required' | 'email' | 'customFunc'
  message: string;
  customFunc?: () => boolean
}
export type RulesType = RuleType[] // 注意导出的是RulesType，是一个数组（包含了若干规则）
export type TagType = 'input' | 'textarea' // 使用union types和literal types，这里必须用type而不是interface

export default defineComponent({
  props: {
    rules: Array as PropType<RulesType>,
    modelValue: String,
    tag: {
      type: String as PropType<TagType>,
      default: 'input'
    }
  },
  inheritAttrs: false, // 设置禁止自动透传，这样可以手动设置
  setup (props, context) {
    // 响应式对象
    const inputRef = reactive({
      val: computed({
        get: () => props.modelValue || '',
        set: val => {
          context.emit('update:modelValue', val)
        }
      }), // 只有props.modelValue不存在时，才为空字符串
      error: false,
      message: ''
    })

    // 验证输入是否有效（符合规则），如果无效，inputRef.error响应式地变成true，触发template中设置的效果
    const validateInput = () => {
      if (props.rules) {
        const allPassed = props.rules.every((rule) => {
          let passed = true
          inputRef.message = rule.message
          switch (rule.type) {
            case 'required':
              passed = (inputRef.val.trim() !== '') // 具体每个规则的验证逻辑
              break
            case 'email':
              passed = emailReg.test(inputRef.val) // 具体每个规则的验证逻辑
              break
            case 'customFunc':
              passed = rule.customFunc ? rule.customFunc() : true // 如果rule.customFunc存在，则运行该函数
              break
            default:
              break
          }
          return passed
        })
        inputRef.error = !allPassed
        return allPassed
      }
      return true
    }
    // 发射ValidateInput函数
    onMounted(() => {
      formMitt.emit('form-item-created', validateInput)
    })
    return {
      inputRef,
      validateInput
    }
  }
})
