import { createApp } from 'vue'
import axios from 'axios'
import router from './router'
import store from './store'
import App from './App.vue'

// 设置 baseURLs
axios.defaults.baseURL = 'http://apis.imooc.com/api/'
// 清除localStorage中的token
// localStorage.removeItem('token')
// axios拦截器，设置icode
axios.interceptors.request.use(config => {
  // get请求，添加到url中，相当于添加了'?icode=B5DE92FA3B028AE5'
  config.params = { ...config.params, icode: 'B5DE92FA3B028AE5' }
  // 其他请求，添加到 body 中
  // 如果是上传文件，添加到 FormData 中
  if (config.data instanceof FormData) {
    config.data.append('icode', 'B5DE92FA3B028AE5')
  } else {
  // 普通的 body 对象，添加到 data 中
    config.data = { ...config.data, icode: 'B5DE92FA3B028AE5' }
  }
  store.commit('setLoading', true) // 设置loading启动
  store.commit('setError', { status: false, message: '' })
  return config
})

axios.interceptors.response.use(config => {
  store.commit('setLoading', false) // 设置loading结束
  return config
}, err => {
  const { error } = err.response.data
  store.commit('setLoading', false)
  store.commit('setError', { status: true, message: error })
})

const app = createApp(App)
app.use(router) // 使用router实例
app.use(store) // 使用store实例
app.mount('#app') // 注意顺序，必须先app.use再app.mount
