
import { defineComponent, computed, onMounted, watch } from 'vue'
import { useStore } from 'vuex'
import axios from 'axios'
import { GlobalDataType } from './store'
import 'bootstrap/dist/css/bootstrap.min.css'
import GlobalHeader from './components/GlobalHeader.vue'
import LoadState from './components/LoadState.vue'
import createMessage from './components/createMessage'

export default defineComponent({
  name: 'App',
  components: {
    GlobalHeader,
    LoadState
  },
  setup () {
    const store = useStore<GlobalDataType>()
    const currentUser = computed(() => store.state.user)
    const loading = computed(() => store.state.loading)
    const error = computed(() => store.state.error)
    // 持久化登录状态，避免每次刷新以后丢失已登录的状态
    onMounted(() => {
      if (store.state.token && !store.state.user.isLogin) {
        axios.defaults.headers.common.Authorization = `Bearer ${store.state.token}`
        store.dispatch('fetchCurrentUser')
      }
    })
    watch(() => error.value.status, () => {
      if (error.value.status && error.value.message) {
        createMessage('error', error.value.message)
      }
    })
    return {
      currentUser,
      loading,
      error
    }
  }
})
