import { createStore, Commit } from 'vuex'
import axios, { AxiosRequestConfig } from 'axios'

export interface ImageType { // 定义图像的数据结构类型（根据后端api作出的改动）
  _id?: string;
  url?: string;
  defaultUrl?: string;
  createdAt?: string;
}
export interface UserType { // 定义User数据结构的类型
  isLogin?: boolean;
  nickName?: string;
  description?: string;
  _id?: string;
  email?: string;
  avatar?: ImageType;
  defaultAvatar?: string;
  column?: string; // user对应的column（一对一）
  createdAt?: string;
}
// eslint-disable-next-line @typescript-eslint/ban-types
export interface ResponseType<T = {}> { // T = {}意思是默认值为空对象
  code: number;
  msg: string;
  data: T;
}

export interface ColumnType { // 定义专栏数据结构的类型
  _id: string;
  avatar?: ImageType;
  title: string;
  description: string;
}

export interface ColumnPostType { // 定义专栏文章数据结构的类型
  _id?: string;
  title: string;
  excerpt?: string; // 是content的节选，固定长度
  content?: string;
  image?: ImageType | string;
  createdAt?: string;
  column: string; // 这个属性是column的id，将ColumnPost和Column联系在一起，比较重要
  author?: string | UserType;
}
export interface GlobalErrorType {
  status: boolean;
  message?: string;
}
export interface GlobalDataType { // 定义全局数据结构的类型
  error: GlobalErrorType;
  token: string;
  loading: boolean;
  user: UserType;
  columns: ColumnType[];
  posts: ColumnPostType[];
}

// 在action里反复出现的逻辑，抽象出一个函数。其中参数commit是Commit类型，可以从vuex中导入
const getAndCommit = async (endpoint: string, mutationName: string, commit: Commit) => {
  const { data } = await axios.get(endpoint) // 解构赋值
  // 通过commit提交mutation
  commit(mutationName, data) // data是传入mutation的参数，也叫载荷
  return data
}
// eslint-disable-next-line @typescript-eslint/no-explicit-any
const postAndCommit = async (endpoint: string, mutationName: string, commit: Commit, payload: any) => {
  const { data } = await axios.post(endpoint, payload) // 为了得到token，需要上传登录信息
  commit(mutationName, data)
  return data
}
const asyncAndCommit = async (endpoint: string, mutationName: string, commit: Commit, config: AxiosRequestConfig = { method: 'get' }) => { // 在config中设置具体是哪一种异步请求
  const { data } = await axios(endpoint, config) // config中除了可以设置具体的请求方法，还可以设置payload等
  commit(mutationName, data)
  return data
}

// 创建store实例
const store = createStore<GlobalDataType>({ // 为什么把GlobalDataType写在这里就可以规定state的类型？
  state: {
    error: { status: false },
    token: localStorage.getItem('token') || '', // 初始化token值
    loading: false,
    user: {
      isLogin: false
    },
    columns: [],
    posts: []
  },
  mutations: { // 主要用来存放state的更改行为
    // login (state) { // login是mutation的一个type
    //   state.user = {
    //     ...state.user, // 解构state.user，如果后面的Key和解构出的key相同，后面的会覆盖前面的
    //     isLogin: true,
    //     name: 'orca',
    //     columnId: '1'
    //   }
    // },
    fetchColumns (state, rawData) { // rawData是传入的载荷，即resp.data
      state.columns = rawData.data.list
    },
    fetchColumn (state, rawData) {
      state.columns = [rawData.data] // 这里直接加方括号，state.columns直接等于某个column
    },
    fetchPosts (state, rawData) {
      state.posts = rawData.data.list
    },
    fetchPost (state, rawData) {
      state.posts = [rawData.data] // 这里直接加方括号，state.columns直接等于某个column
    },
    setLoading (state, status) {
      state.loading = status
    },
    setError (state, error) {
      state.error = error
    },
    createPost (state, newPost) { // newPost是createPost的载荷（payload）
      state.posts.push(newPost)
    },
    updatePost (state, { data }) {
      state.posts = state.posts.map((post) => {
        if (post._id === data._id) {
          return data
        } else {
          return post
        }
      })
    },
    deletePost (state, { data }) {
      state.posts = state.posts.filter(post => post._id !== data._id) // 使用数组的filter方法
    },
    login (state, rawData) { // 这里需要更新state中的token，更新axios请求头
      const { token } = rawData.data
      state.token = token // 拿到token以后在state中写入
      localStorage.setItem('token', token) // 拿到token以后在localStorage中写入
      axios.defaults.headers.common.Authorization = `Bearer ${token}` // // 拿到token以后在请求头中写入（Bearer是一个固定格式）
    },
    fetchCurrentUser (state, rawData) {
      state.user = { isLogin: true, ...rawData.data } // 注意要修改isLogin的值
    },
    logout (state) {
      // 对照login mutation的内容一条条删除token
      state.token = ''
      localStorage.removeItem('token')
      delete axios.defaults.headers.common.Authorization
      // user的状态变为登出
      state.user.isLogin = false
    }
  },
  actions: { // 主要用来存放和后端的交互行为，通常是异步的
    fetchColumns ({ commit }) { // 从context参数中解构出commit属性（方法）
      return asyncAndCommit('/columns?pageSize=9', 'fetchColumns', commit, { method: 'get' })
    },
    fetchColumn ({ commit }, cid) { // 与fetchColumns类似，但是fetchColumn需要传入columnId参数
      return getAndCommit(`/columns/${cid}`, 'fetchColumn', commit) // 注意这里使用模板字符串传入变量cid
    },
    fetchPosts ({ commit }, cid) { // 与fetchColumn类似
      return getAndCommit(`/columns/${cid}/posts`, 'fetchPosts', commit) // 注意这里使用模板字符串传入变量cid
    },
    createPost ({ commit }, payload) {
      return postAndCommit('posts', 'createPost', commit, payload)
    },
    fetchPost ({ commit }, pid) {
      return getAndCommit(`/posts/${pid}`, 'fetchPost', commit)
    },
    updatePost ({ commit }, { id, payload }) {
      return asyncAndCommit(`/posts/${id}`, 'updatePost', commit, { method: 'patch', data: payload })
    },
    deletePost ({ commit }, pid) {
      return asyncAndCommit(`/posts/${pid}`, 'deletePost', commit, { method: 'delete' })
    },
    login ({ commit }, payload) { // payload是传入的登录数据（邮箱和密码）
      return postAndCommit('user/login', 'login', commit, payload) // postAndCommit中返回了data，所以要加return
    },
    fetchCurrentUser ({ commit }) {
      return getAndCommit('/user/current', 'fetchCurrentUser', commit)
    },
    loginAndFetch ({ dispatch }, loginData) {
      return dispatch('login', loginData).then(() => { // 'login' action返回了data，所以要加return
        return dispatch('fetchCurrentUser')
      })
    }
  },
  getters: {
    findColumnById: (state) => (id:string) => {
      return state.columns.find((column) => column._id === id)
    },
    filterPostById: (state) => (id:string) => {
      return state.posts.filter((post) => post.column === id)
    }
  }
})

export default store
