import { render, h } from 'vue'
import MessageBox, { MessageType } from './MessageBox.vue'

const createMessage = (type: MessageType, message: string, timeout = 2000) => {
  const messageVnode = h(MessageBox, {
    type,
    message
  })
  // 如果把massageInstance挂载到div上，就不用在MessageBox中使用teleport了
  const mountNode = document.createElement('div')
  document.body.appendChild(mountNode)
  render(messageVnode, mountNode) // 使用render函数挂载
  setTimeout(() => {
    render(null, mountNode) // 使用render函数卸载
    document.body.removeChild(mountNode)
  }, timeout)
}

export default createMessage
