
import { defineComponent, computed, onMounted } from 'vue'
import { useStore } from 'vuex'
import { GlobalDataType } from '@/store'
import ColumnList from '../components/ColumnList.vue'

export default defineComponent({
  name: 'HomeView',
  components: {
    ColumnList
  },
  setup () {
    const store = useStore<GlobalDataType>()
    onMounted(() => {
      store.dispatch('fetchColumns') // 发送请求，获取后端数据
    })
    const column = computed(() => store.state.columns) // 不要忘记computed，数据是动态变化的
    return {
      column
    }
  }
})
