
import { defineComponent, onMounted, computed, ref } from 'vue'
import { useRoute } from 'vue-router'
import store, { UserType, ResponseType, ColumnPostType } from '@/store'
import MarkdownIt from 'markdown-it'
import UserProfile from '@/components/UserProfile.vue'
import ModalBox from '@/components/ModalBox.vue'
import createMessage from '@/components/createMessage'
import router from '@/router'

export default defineComponent({
  components: {
    UserProfile,
    ModalBox
  },
  setup () {
    const route = useRoute()
    const postId = route.params.id
    const modalVisibility = ref(false)
    const md = new MarkdownIt()

    onMounted(() => {
      store.dispatch('fetchPost', postId)
    })
    const currentPost = computed(() => {
      console.log(store.state.posts[0])
      return store.state.posts[0]
    }) // 如果不使用computed，在进入第二篇文章时无法更新

    const currentHTML = computed(() => {
      if (currentPost.value && currentPost.value.content) {
        return md.render(currentPost.value.content)
      }
      return ''
    })

    const showEditArea = computed(() => { // 判断用户是否有权限进行编辑和删除操作
      const { isLogin, _id } = store.state.user // 获得当前用户是否登录和id的信息，注意这个必须写在computed里面，因为store中user的值是通过异步请求获得的，需要被响应式更新
      if (currentPost.value && currentPost.value.author && isLogin) {
        const postAuthor = currentPost.value.author as UserType
        return _id === postAuthor._id
      } else {
        return false
      }
    })

    const hideAndDelete = () => {
      modalVisibility.value = false // 隐藏modalBox
      store.dispatch('deletePost', postId).then((rawData: ResponseType<ColumnPostType>) => {
        createMessage('success', '删除文章成功 2秒后跳转专栏')
        setTimeout(() => {
          router.push({ name: 'column', params: { id: rawData.data.column } })
        }, 2000)
      })
    }
    return {
      modalVisibility,
      currentPost,
      currentHTML,
      showEditArea,
      hideAndDelete
    }
  }
})
